
import icomoonSelection from './../font/icons/selection.json'

export const iconList = icomoonSelection.icons.map((icon) => icon.properties.name);

export const iconOptions = iconList.reduce((acc, icon) => {
    acc[icon] = icon.charAt(0).toUpperCase() + icon.slice(1); // Capitalizza il nome dell'icona
    return acc;
  }, {});

